import React from 'react';
import PropTypes from 'prop-types';
import { Button, Form, Grid, Header, Segment } from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import { connect } from 'react-redux';
import { signup } from '../../redux/actions/user';
import { withRouter } from 'react-router-dom';

const SignupPage = ({ signup, history }) => {
  const [formData, setFormData] = useImmer({
    name: '',
    email: '',
    password: '',
    password2: '',
  });
  const [formErrors, setFormErrors] = useImmer({
    errors: {},
  });

  const onChange = async (e) => {
    setFormData((draft) => {
      draft[e.target.name] = e.target.value;
    });
  };

  const validateForm = () => {
    const errors = {};
    if (formData.name === '') {
      errors.name = 'Missing name.';
    }
    if (formData.email === '') {
      errors.email = 'Missing email.';
    }
    if (formData.password === '') {
      errors.password = 'Missing password.';
    }
    if (formData.password.length < 6) {
      errors.password = 'Password length must be 6 characters or more.';
    }

    if (formData.password2 === '') {
      errors.password2 = 'Missing password.';
    }
    if (formData.password !== formData.password2) {
      errors.password2 = 'Password does not match.';
    }
    if (Object.keys(errors).length !== 0) {
      setFormErrors((draft) => {
        draft.errors = errors;
      });
      return false;
    }
    return true;
  };

  const onSubmit = (e) => {
    e.preventDefault();
    setFormErrors((draft) => {
      draft.errors = {};
    });

    const isValid = validateForm();
    if (isValid) {
      console.log('FormData:', formData);
      signup(formData, history);
    } else {
      console.log('Form incomplete');
    }
  };
  return (
    <Grid textAlign="center" style={{ height: '100vh' }} verticalAlign="middle">
      <Grid.Column style={{ maxWidth: 450 }}>
        <Header as="h2" color="teal" textAlign="center">
          Signup
        </Header>
        <Form size="large" onSubmit={onSubmit}>
          <Segment stacked>
            <Form.Input
              fluid
              icon="wheelchair"
              iconPosition="left"
              placeholder="Name"
              name="name"
              onChange={onChange}
            />
            {formErrors.errors.name}
            <p>(@u.nus.edu|@ntu.edu.sg|@smu.edu.sg)</p>
            <Form.Input
              fluid
              icon="user"
              iconPosition="left"
              placeholder="School email address"
              name="email"
              onChange={onChange}
            />

            {formErrors.errors.email}
            <Form.Input
              fluid
              icon="lock"
              iconPosition="left"
              placeholder="Password"
              type="password"
              name="password"
              onChange={onChange}
            />
            {formErrors.errors.password}
            <Form.Input
              fluid
              icon="lock"
              iconPosition="left"
              placeholder="Re-type password"
              type="password"
              name="password2"
              onChange={onChange}
            />
            {formErrors.errors.password2}

            <Button color="blue" fluid size="large" type="submit">
              Sign up
            </Button>
          </Segment>
        </Form>
      </Grid.Column>
    </Grid>
  );
};

SignupPage.propTypes = {
  signup: PropTypes.func.isRequired,
};

export default connect(null, { signup })(withRouter(SignupPage));
