import React, { Fragment } from 'react';
import {
  Grid,
  Button,
  Checkbox,
  Form,
  Input,
  Label,
  TextArea,
  Item,
  Card,
  Header,
} from 'semantic-ui-react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { useImmer } from 'use-immer';
import axios from 'axios';

const CreateEditProfile = ({ user, history }) => {
  const [name, setName] = useImmer(user.username ? user.username : '');
  const [bio, setBio] = useImmer(
    user.otherThings.bio ? user.otherThings.bio : ''
  );
  //const [skills, setSkills] = useImmer([]);
  const [skills, setSkills] = useImmer(
    user.otherThings.skillsets ? user.otherThings.skillsets : []
  );
  //const [experiences, setExperiences] = useImmer([]);
  const [experiences, setExperiences] = useImmer(
    user.otherThings.experiences ? user.otherThings.experiences : []
  );
  /*const [socials, setSocials] = useImmer({
    youtube: '',
    twitter: '',
    facebook: '',
    instagram: '',
  });*/
  const [socials, setSocials] = useImmer({
    youtube:
      user.otherThings.social &&
      (user.otherThings.social.youtube ? user.otherThings.social.youtube : ''),
    twitter:
      user.otherThings.social &&
      (user.otherThings.social.twitter ? user.otherThings.social.twitter : ''),
    facebook:
      user.otherThings.social &&
      (user.otherThings.social.facebook
        ? user.otherThings.social.facebook
        : ''),
    instagram:
      user.otherThings.social &&
      (user.otherThings.social.instagram
        ? user.otherThings.social.instagram
        : ''),
  });
  const [website, setWebsite] = useImmer('');
  const [company, setCompany] = useImmer('');

  const onAddSkills = (e) => {
    e.preventDefault();
    setSkills((draft) => void draft.push(''));
  };
  const onSkillChange = (e) => {
    console.log(e.target.id);
    setSkills((draft) => {
      draft[e.target.id] = e.target.value;
    });
  };
  const onSkillDelete = (e) => {
    e.preventDefault();
    setSkills((draft) => void draft.splice(e.target.value, 1));
  };

  const onAddExp = (e) => {
    e.preventDefault();
    setExperiences((draft) => {
      draft.push({
        title: '',
        company: '',
        description: '',
      });
    });
  };

  const onExpChange = (e) => {
    setExperiences((draft) => {
      draft[e.target.id][e.target.name] = e.target.value;
    });
  };

  const onExpDelete = (e) => {
    e.preventDefault();
    setExperiences((draft) => {
      draft.splice(e.target.id, 1);
    });
  };

  const onSocialChange = (e) => {
    setSocials((draft) => {
      draft[e.target.name] = e.target.value;
    });
  };

  const onWebsiteChange = (e) => {
    setWebsite((draft) => e.target.value);
  };

  const onCompanyChange = (e) => {
    setCompany((draft) => e.target.value);
  };

  const onNameChange = (e) => {
    setName((draft) => e.target.value);
  };
  const onBioChange = (e) => {
    setBio((draft) => e.target.value);
  };

  const onSubmit = async (e) => {
    try {
      e.preventDefault();
      const formData = {
        name: name,
        bio: bio,
        skills: skills,
        experiences: experiences,
        socialmedias: socials,
        website: website,
        company: company,
      };
      console.log('Form Data: ', formData);
      const submitForm = await axios.post(
        '/api/users/pushprofiledata',
        formData
      );
      console.log('Submitted Form: ', submitForm);
      history.push(`/profile/${user.userid}`);
    } catch (err) {
      console.error('Error: ', err.response.data);
    }
  };

  return (
    <Grid textAlign="center" verticalAlign="middle" style={{ height: '100vh' }}>
      <Grid.Column style={{ maxWidth: 550 }}>
        <Form onSubmit={onSubmit}>
          <Form.Field>
            <label>Name</label>
            <input
              placeholder="First Name"
              name="name"
              value={name}
              onChange={onNameChange}
            />
          </Form.Field>
          <Form.Field>
            <label>Bio</label>
            <TextArea
              placeholder="Bio"
              name="bio"
              value={bio}
              onChange={onBioChange}
            />
          </Form.Field>
          <Form.Field>
            <label>Skill Sets</label>
            <Button color="blue" onClick={onAddSkills}>
              Add
            </Button>
            {skills.map((skill, index) => (
              <Fragment key={index}>
                <Input
                  label
                  value={skill}
                  id={index}
                  key={index}
                  onChange={onSkillChange}
                >
                  <Label>{index + 1}</Label>
                  <input />
                  <Button value={index} onClick={onSkillDelete}>
                    Delete
                  </Button>
                </Input>
                <br />
                <br />
              </Fragment>
            ))}
          </Form.Field>
          <Form.Field>
            <label>Experiences</label>
            <Button color="blue" onClick={onAddExp}>
              Add
            </Button>
            <Item.Group style={{ textAlign: 'left' }}>
              {experiences.map((experience, index) => (
                <Fragment key={index}>
                  {/* <Item>
                    <Item.Content>
                      <Item.Header>{experience.title}</Item.Header>
                      <Item.Meta>{experience.company}</Item.Meta>
                      <Item.Description>
                        {experience.description}
                      </Item.Description>
                    </Item.Content>
                 </Item>*/}
                  <Header as="h3">{index + 1}.</Header>
                  <Input
                    label
                    value={experience.title}
                    onChange={onExpChange}
                    id={index}
                    name="title"
                  >
                    <Label>Title</Label>
                    <input />
                  </Input>
                  <Input
                    label
                    value={experience.company}
                    onChange={onExpChange}
                    id={index}
                    name="company"
                  >
                    <Label>Company</Label>
                    <input />
                  </Input>
                  <Input
                    label
                    value={experience.description}
                    onChange={onExpChange}
                    id={index}
                    name="description"
                  >
                    <Label>Description</Label>
                    <input />
                  </Input>
                  <Button color="red" fluid id={index} onClick={onExpDelete}>
                    Delete
                  </Button>
                </Fragment>
              ))}
            </Item.Group>
          </Form.Field>
          <Form.Field>
            <label>Socials</label>
            <Input
              label="youtube"
              placeholder="Youtube"
              name="youtube"
              onChange={onSocialChange}
              value={socials.youtube}
            />
            <Input
              label="twitter"
              placeholder="Twitter"
              name="twitter"
              onChange={onSocialChange}
              value={socials.twitter}
            />
            <Input
              label="facebook"
              placeholder="Facebook"
              name="facebook"
              onChange={onSocialChange}
              value={socials.facebook}
            />
            <Input
              label="instagram"
              placeholder="instagram"
              name="instagram"
              onChange={onSocialChange}
              value={socials.instagram}
            />
          </Form.Field>
          <Form.Field>
            <label>Website</label>
            <input
              placeholder="Website"
              value={website}
              onChange={onWebsiteChange}
            />
          </Form.Field>
          <Form.Field>
            <label>Company</label>
            <input
              placeholder="Company"
              value={company}
              onChange={onCompanyChange}
            />
          </Form.Field>

          <Button type="submit" color="black">
            Submit
          </Button>
        </Form>
      </Grid.Column>
    </Grid>
  );
};

const mapStateToProps = (state) => ({
  user: state.user,
});

CreateEditProfile.propTypes = {
  user: PropTypes.object.isRequired,
};

export default connect(mapStateToProps)(CreateEditProfile);
