import React, { useEffect, Fragment } from 'react';
import { Grid, Header, Icon, Table, Item, Button } from 'semantic-ui-react';
import axios from 'axios';
import { useImmer } from 'use-immer';
import { Link } from 'react-router-dom';
const MyMessages = () => {
  useEffect(() => {
    const retrieveMsg = async () => {
      try {
        const getMsg = await axios.get('/api/users/messages');
        setMessages((draft) => getMsg.data.messages);
        setLoading((draft) => false);
      } catch (err) {
        console.error(err.response);
      }
    };
    retrieveMsg();
  }, []);
  const [messages, setMessages] = useImmer([]);
  const [loading, setLoading] = useImmer(true);
  return (
    <Grid textAlign="center" verticalAlign="middle" style={{ height: '100vh' }}>
      <Grid.Column style={{ maxWidth: '650px' }}>
        <Header as="h1">Messages</Header>
        <Table celled padded>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Content</Table.HeaderCell>
              <Table.HeaderCell>From</Table.HeaderCell>
              <Table.HeaderCell>Date</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {!loading && (
              <Fragment>
                {messages.map((msg, index) => (
                  <Table.Row key={index}>
                    <Table.Cell>{msg.content}</Table.Cell>
                    <Table.Cell>
                      <Link to={`/profile/${msg.from._id}`}>
                        {msg.from.name}
                      </Link>
                    </Table.Cell>

                    <Table.Cell>{msg.date}</Table.Cell>
                    <Table.Cell>
                      <Button color="black">Reply</Button>
                    </Table.Cell>
                  </Table.Row>
                ))}
              </Fragment>
            )}
          </Table.Body>
        </Table>
      </Grid.Column>
    </Grid>
  );
};

export default MyMessages;
