import React, { useEffect, Fragment } from 'react';
import { DateTime } from 'luxon';
import { useImmer } from 'use-immer';
import axios from 'axios';
import { Header } from 'semantic-ui-react';

/*
    desc: Display server time and next tick duration and current server tick number
*/

const dateSetting = {
  timezone: 'Asia/Singapore',
  locale: 'en-US',
  format: 'tt',
  localeString: DateTime.DATETIME_FULL_WITH_SECONDS,
};

const ServerTime = () => {
  const [serverTime, setServerTime] = useImmer({
    serverTime: DateTime.local()
      .setZone(dateSetting.timezone)
      .setLocale(dateSetting.locale)
      .toFormat(dateSetting.format)
      .toLocaleString(dateSetting.localeString),
  });
  const [serverTicker, setServerTicker] = useImmer({
    serverID: '',
    serverTicker: '',
    isLoading: true,
  });
  const [serverDoneTicking, setServerDoneTicking] = useImmer({
    done: false,
  });
  useEffect(() => {
    //isMountedRef.current = true;
    const interval = setInterval(() => {
      setServerTime((draft) => {
        draft.serverTime = DateTime.local()
          .setZone(dateSetting.timezone)
          .setLocale(dateSetting.locale)
          .toFormat(dateSetting.format)
          .toLocaleString(dateSetting.localeString);
      });
    }, 1000);

    return () => {
      //isMountedRef.current = false;
      clearInterval(interval);
    };
  }, [setServerTime]);

  useEffect(() => {
    const getServerTicker = async () => {
      const serverTick = await axios.get('/api/controlpanel/getstick');
      setServerTicker((draft) => {
        draft.serverID = serverTick.data._id;
        draft.serverTicker = serverTick.data.tick;
        draft.isLoading = false;
      });
    };
    getServerTicker();
  }, [setServerTicker]);

  const plusServerTicker = async () => {
    if (!serverDoneTicking.done) {
      setServerDoneTicking((draft) => {
        draft.done = true;
      });
      setServerTicker((draft) => {
        draft.serverTicker = draft.serverTicker + 1;
      });
      //await axios.post('/api/controlpanel/plusstick', serverTicker);
    }
  };

  const tickerTimer = () => {
    const now = DateTime.local()
      .setZone('Asia/Singapore')
      .setLocale('en-US')
      .toFormat('m');

    if (now === '30' || now === '0') {
      plusServerTicker();
      return 30;
    }
    //0 > x < 60
    if (parseInt(now) > 0 && parseInt(now) < 60 && serverDoneTicking.done) {
      setServerDoneTicking((draft) => {
        draft.done = false;
      });
    }
    if (parseInt(now) >= 0 && parseInt(now) < 30 && !serverDoneTicking.done) {
      return 30 - parseInt(now);
    }
    // 30<x<60
    if (parseInt(now) >= 30 && parseInt(now) < 60 && !serverDoneTicking.done) {
      return 60 - parseInt(now);
    }
  };

  const onClick = (e) => {
    e.preventDefault();
    plusServerTicker();
  };
  return (
    <Fragment>
      {serverTicker.isLoading ? null : (
        <Header as="h4" style={{ color: 'white' }}>
          {serverTime.serverTime}
          <br />
          Next Tick In: [{tickerTimer()} mins]
          {/*<h4>Server Tick: {serverTicker.serverTicker}</h4>*/}
        </Header>
      )}
    </Fragment>
  );
};

export default ServerTime;
