import React, { useEffect } from 'react';
import { Grid, Header, Icon, Table, Item, Button } from 'semantic-ui-react';
import axios from 'axios';
import { useImmer } from 'use-immer';
import { Link } from 'react-router-dom';

const NewsPage = () => {
  useEffect(() => {
    const getLogs = async () => {
      try {
        const logs = await axios.get('/api/users/getnews');
        console.log(logs.data.AnDLogs);
        setUserLogs((draft) => logs.data.AnDLogs);
      } catch (err) {
        console.error(err.response);
      }
    };
    getLogs();
  }, []);
  const [userLogs, setUserLogs] = useImmer([]);
  return (
    <Grid textAlign="center" verticalAlign="middle" style={{ height: '100vh' }}>
      <Grid.Column style={{ maxWidth: '750px' }}>
        <Header as="h1">News</Header>
        <Table celled padded>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Type</Table.HeaderCell>
              <Table.HeaderCell>Result</Table.HeaderCell>
              <Table.HeaderCell>Description</Table.HeaderCell>
              <Table.HeaderCell>From</Table.HeaderCell>
              <Table.HeaderCell>Date</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          {userLogs.map((log) => (
            <Table.Body>
              <Table.Row>
                <Table.Cell>
                  <Header as="h2" textAlign="center">
                    {log.type}
                  </Header>
                </Table.Cell>
                <Table.Cell>
                  <Header as="h2" textAlign="center">
                    {log.result}
                  </Header>
                </Table.Cell>
                <Table.Cell>{log.description}</Table.Cell>
                <Table.Cell>
                  <Link to={`/profile/${log.from._id}`}>{log.from.name}</Link>
                </Table.Cell>
                <Table.Cell>{log.date}</Table.Cell>
              </Table.Row>
            </Table.Body>
          ))}
        </Table>
      </Grid.Column>
    </Grid>
  );
};

export default NewsPage;
