import React, { Fragment } from 'react';
import {
  Button,
  Form,
  Grid,
  Header,
  Message,
  Segment,
} from 'semantic-ui-react';
import { connect } from 'react-redux';
import { loginUser } from '../../redux/actions/user';
import PropTypes from 'prop-types';
import { useImmer } from 'use-immer';
import { withRouter, Redirect, Link } from 'react-router-dom';
import ServerTime from '../ServerTime/ServerTime';
import { Helmet } from 'react-helmet';

const AuthPage = ({ loginUser, history, user }) => {
  const [formData, setFormData] = useImmer({
    email: '',
    password: '',
  });

  const [formErrors, setFormErrors] = useImmer({
    errors: {},
  });

  const onChange = (e) => {
    setFormData((draft) => {
      draft[e.target.name] = e.target.value;
    });

    /*setFormData(
      produce((draft) => {
        draft[e.target.name] = e.target.value;
      })
    );*/
    //setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  const checkForm = () => {
    const errors = {};
    if (formData.email === '' || !formData.email.includes('@')) {
      errors.email = 'Email field is invalid.';
    }
    if (formData.password === '') {
      errors.password = 'Password field is empty.';
    }
    if (Object.keys(errors).length !== 0) {
      setFormErrors((draft) => {
        draft.errors = errors;
      });
      return false;
    }
    return true;
  };

  const onSubmit = (e) => {
    e.preventDefault();
    setFormErrors((draft) => {
      draft.errors = {};
    });
    const isValid = checkForm();
    if (isValid) {
      loginUser(formData, history);
    }
  };
  return (
    <Fragment>
      <Helmet>
        <title>Login Page</title>
      </Helmet>
      {!user.isAuthenticated ? (
        <Grid
          textAlign="center"
          style={{ height: '100vh' }}
          verticalAlign="middle"
        >
          <Grid.Column style={{ maxWidth: 450 }}>
            <ServerTime />
            <Header as="h2" color="teal" textAlign="center">
              Log-in to your account
            </Header>
            <Form size="large" onSubmit={onSubmit}>
              <Segment stacked>
                <Form.Input
                  fluid
                  icon="user"
                  iconPosition="left"
                  placeholder="E-mail address"
                  name="email"
                  value={formData.email}
                  onChange={onChange}
                />
                {formErrors.errors.email}
                <Form.Input
                  fluid
                  icon="lock"
                  iconPosition="left"
                  placeholder="Password"
                  type="password"
                  name="password"
                  value={formData.password}
                  onChange={onChange}
                />
                {formErrors.errors.password}

                <Button color="teal" fluid size="large" type="submit">
                  Login
                </Button>
              </Segment>
            </Form>
            <Message>
              New to us? <Link to="/signup">Sign Up</Link>
            </Message>
          </Grid.Column>
        </Grid>
      ) : (
        <Redirect to={`/homepage`} />
      )}
    </Fragment>
  );
};

AuthPage.propTypes = {
  loginUser: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  user: state.user,
});

export default connect(mapStateToProps, { loginUser })(withRouter(AuthPage));
