import React from 'react';
import {
  Grid,
  Header,
  Form,
  TextArea,
  Button,
  Comment,
  Card,
} from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import axios from 'axios';

const Feedback = () => {
  const [feedback, setFeedback] = useImmer('');
  const onChange = (e) => {
    setFeedback((draft) => e.target.value);
  };
  const onSubmit = async (e) => {
    try {
      e.preventDefault();
      const result = await axios.post('/api/users/feedback', { feedback });

      alert('Thank you.');
      setFeedback((draft) => '');
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };
  return (
    <Grid textAlign="center" style={{ height: '100vh' }} verticalAlign="middle">
      <Grid.Column style={{ maxWidth: 650, float: 'left' }}>
        <Header as="h2">Feedback</Header>
        <Form onSubmit={onSubmit}>
          <TextArea
            placeholder="Feedback"
            value={feedback}
            onChange={onChange}
          />
          <Button type="submit">Submit</Button>
        </Form>
      </Grid.Column>
    </Grid>
  );
};

export default Feedback;
