import React, { Fragment, useEffect } from 'react';
import { useImmer } from 'use-immer';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Helmet } from 'react-helmet';
import { Redirect } from 'react-router-dom';
import ServerTime from '../ServerTime/ServerTime';
import { getAllItemsByEmail } from '../../redux/actions/items';
import { Card, Grid } from 'semantic-ui-react';
const ControlPanel = ({ isAuth, getAllItemsByEmail, userItems }) => {
  const [theItems, setTheItems] = useImmer({
    items: [],
    isLoading: true,
  });
  useEffect(() => {
    getAllItemsByEmail();
  }, [getAllItemsByEmail]);

  useEffect(() => {
    if (!userItems.isLoading) {
      setTheItems((draft) => {
        draft.items = userItems.data.items;
        draft.isLoading = false;
      });
    } else {
      setTheItems((draft) => {
        draft.items = [];
        draft.isLoading = true;
      });
    }
    // eslint-disable-next-line
  }, [userItems.isLoading]);
  return (
    <Grid textAlign="center" verticalAlign="middle" style={{ height: '100vh' }}>
      <Grid.Column style={{ maxWidth: 450 }}>
        <Helmet>
          <title>Control Panel</title>
        </Helmet>
        {!isAuth ? (
          <Redirect to="/login" />
        ) : (
          <Fragment>
            <h1>ControlPanel</h1>
            <ServerTime />
            {theItems.isLoading ? (
              <h3>Loading Items...</h3>
            ) : (
              <Fragment>
                {theItems.items.length === 0 ? (
                  <h3>No items....</h3>
                ) : (
                  <Card.Group centered={true}>
                    {theItems.items.map((item) => (
                      <Card key={item._id}>
                        <Card.Content>
                          <Card.Header>{item.itemName}</Card.Header>
                          <Card.Meta>x{item.quantity}</Card.Meta>
                          <Card.Description>
                            {item.additionalNote}
                          </Card.Description>
                        </Card.Content>
                      </Card>
                    ))}
                  </Card.Group>
                )}
              </Fragment>
            )}
          </Fragment>
        )}
      </Grid.Column>
    </Grid>
  );
};

ControlPanel.propTypes = {
  isAuth: PropTypes.bool.isRequired,
  getAllItemsByEmail: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  isAuth: state.user.isAuthenticated,
  userItems: state.items,
});

export default connect(mapStateToProps, { getAllItemsByEmail })(ControlPanel);
