import React, { Fragment, useEffect } from 'react';
import {
  Grid,
  Header,
  Button,
  Modal,
  Icon,
  Table,
  Item,
  TextArea,
} from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import axios from 'axios';

const ProfilePage = ({ userID, match, history }) => {
  useEffect(() => {
    const getUserInfo = async () => {
      try {
        const userInfo = await axios.get(
          `/api/users/getuser/${match.params.userid}`
        );
        console.log(userInfo.data);
        setUser((draft) => userInfo.data);
      } catch (err) {
        console.log(err.response.data);
      }
      setLoading((draft) => false);
    };
    getUserInfo();
  }, []);
  const [user, setUser] = useImmer({});
  const [loading, setLoading] = useImmer(true);
  const [open, setOpen] = React.useState(false);
  const [tabs, setTabs] = useImmer({
    profile: true,
    battle: false,
  });
  const [message, setMessage] = useImmer('');
  const goCreateProfile = (e) => {
    e.preventDefault();
    history.push('/ceprofile');
  };

  const onTab = (e) => {
    e.preventDefault();
    setTabs((draft) => {
      draft[e.target.name] = true;
      if (e.target.name === 'profile') {
        draft.battle = false;
      } else {
        draft.profile = false;
      }
    });
  };
  const onMessageChange = (e) => {
    setMessage((draft) => e.target.value);
  };
  const onMessage = async (e) => {
    try {
      e.preventDefault();
      const messageUser = await axios.post(
        `/api/users/messageUser/${user._id}`,
        { content: message }
      );
      console.log(messageUser.data);
    } catch (err) {
      console.error(err.response);
    }
  };
  return (
    <Fragment>
      <Helmet>
        <title>Profile</title>
      </Helmet>
      <Grid
        textAlign="center"
        verticalAlign="middle"
        style={{ height: '100vh' }}
        columns={3}
        divided
      >
        <Grid.Row style={{ maxWidth: 850 }}>
          {loading ? (
            <Header>Loading...</Header>
          ) : (
            <Fragment>
              <Grid.Column>
                <Button name="profile" onClick={onTab}>
                  Profile
                </Button>
                <Button name="battle" onClick={onTab}>
                  Battle Stats
                </Button>
              </Grid.Column>
              {tabs.profile && (
                <>
                  <Grid.Column>
                    <Header as="h4">Name: {user.name}</Header>
                    <Header as="h4">Current School: {user.school}</Header>
                    <Header as="h4">Country: {user.country}</Header>
                    {userID === user._id ? (
                      <Button color="black" onClick={goCreateProfile}>
                        Create/Edit Profile
                      </Button>
                    ) : (
                      <>
                        <Modal
                          onClose={() => setOpen(false)}
                          onOpen={() => setOpen(true)}
                          open={open}
                          trigger={<Button color="black">Message</Button>}
                        >
                          <Modal.Header>Message</Modal.Header>
                          <Modal.Content>
                            <Modal.Description>
                              <Header subheader>To: {user.name}</Header>
                              <TextArea
                                style={{ width: '100%' }}
                                onChange={onMessageChange}
                                value={message}
                              />
                            </Modal.Description>
                          </Modal.Content>
                          <Modal.Actions>
                            <Button
                              color="black"
                              onClick={() => setOpen(false)}
                            >
                              Close
                            </Button>
                            <Button
                              content="Send"
                              onClick={(e) => {
                                setOpen(false);
                                onMessage(e);
                              }}
                              positive
                            />
                          </Modal.Actions>
                        </Modal>
                      </>
                    )}
                  </Grid.Column>
                  <Grid.Column>
                    <Header as="h2">Bio</Header>
                    {user.bio === '' || !user.bio ? (
                      <div style={{ overflowY: 'auto', maxHeight: '25vh' }}>
                        <Header sub>No Bio</Header>
                      </div>
                    ) : (
                      <div style={{ overflowY: 'auto', maxHeight: '25vh' }}>
                        <Header as="h4">{user.bio}</Header>
                      </div>
                    )}
                    <Header as="h2">Skill sets</Header>
                    {user.skillsets.length === 0 ? (
                      <div style={{ overflowY: 'auto', maxHeight: '25vh' }}>
                        <Header sub>No Skills Sets</Header>
                      </div>
                    ) : (
                      <div
                        style={{
                          overflowY: 'auto',
                          maxHeight: '25vh',
                          textAlign: 'left',
                        }}
                      >
                        <Item.Group>
                          {user.skillsets.map((skill, index) => (
                            <Item key={index}>
                              <Item.Content>
                                <Item.Header>
                                  {index + 1}.{skill}
                                </Item.Header>
                              </Item.Content>
                            </Item>
                          ))}
                        </Item.Group>
                      </div>
                    )}
                    <Header as="h2">Experiences</Header>
                    {user.experiences.length === 0 ? (
                      <div style={{ overflowY: 'auto', maxHeight: '25vh' }}>
                        <Header sub>No experiences</Header>
                      </div>
                    ) : (
                      <div
                        style={{
                          overflowY: 'auto',
                          maxHeight: '25vh',
                          textAlign: 'left',
                        }}
                      >
                        <Item.Group>
                          {user.experiences.map((experience, index) => (
                            <Item>
                              <Item.Content>
                                <Item.Header>
                                  {index + 1}. {experience.title}
                                </Item.Header>
                                <Item.Meta>{experience.company}</Item.Meta>
                                <Item.Description>
                                  {experience.description}
                                </Item.Description>
                                {experience.current ? (
                                  <Item.Extra>{experience.from}</Item.Extra>
                                ) : (
                                  <Item.Extra>1999/10/19 - now</Item.Extra>
                                )}
                              </Item.Content>
                            </Item>
                          ))}
                        </Item.Group>
                      </div>
                    )}
                  </Grid.Column>
                </>
              )}
              {tabs.battle && (
                <Grid.Column>
                  <Header as="h4">Networth: {user.networth}</Header>
                  <Header as="h4">
                    Wins / Losses: {user.battleStats.wins}/
                    {user.battleStats.losses}
                  </Header>
                  {match.params.userid !== user.userid && (
                    <Fragment>
                      {userID !== user._id && (
                        <>
                          <Button
                            color="black"
                            onClick={() => {
                              history.push(`/attack/${match.params.userid}`);
                            }}
                          >
                            Attack
                          </Button>
                          <br />
                          <br />
                          <Button
                            color="black"
                            onClick={() => {
                              history.push(
                                `/intelligence/${match.params.userid}`
                              );
                            }}
                          >
                            Gather Intelligence
                          </Button>
                          <br />
                          <br />
                        </>
                      )}
                    </Fragment>
                  )}

                  <Modal
                    open={open}
                    onClose={() => setOpen(false)}
                    onOpen={() => setOpen(true)}
                    trigger={<Button color="black">Recent Battles</Button>}
                  >
                    <Modal.Header>Recent Battles</Modal.Header>
                    <Modal.Content scrolling>
                      <Modal.Description>
                        <Table celled padded>
                          <Table.Header>
                            <Table.Row>
                              <Table.HeaderCell>Type</Table.HeaderCell>
                              <Table.HeaderCell>Result</Table.HeaderCell>
                              <Table.HeaderCell>Description</Table.HeaderCell>
                              <Table.HeaderCell>Date</Table.HeaderCell>
                            </Table.Row>
                          </Table.Header>
                          <Table.Body>
                            <Table.Row>
                              <Table.Cell>
                                <Header>Attack</Header>
                              </Table.Cell>
                              <Table.Cell>Lost</Table.Cell>
                              <Table.Cell>
                                <Table.Cell>
                                  Target: username(clickable).
                                  <br /> 5000 land gained.
                                  <br /> Destroyed 500 units.
                                  <br /> Lost 50 units.
                                </Table.Cell>
                              </Table.Cell>
                              <Table.Cell>2020/10/10 | 5:44PM</Table.Cell>
                            </Table.Row>
                          </Table.Body>
                        </Table>
                      </Modal.Description>
                    </Modal.Content>
                    <Modal.Actions>
                      <Button onClick={() => setOpen(false)} color="black">
                        Close <Icon name="close" />
                      </Button>
                    </Modal.Actions>
                  </Modal>
                </Grid.Column>
              )}
            </Fragment>
          )}
        </Grid.Row>
      </Grid>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  userID: state.user.userid,
});

export default connect(mapStateToProps)(ProfilePage);
