import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Redirect, Link } from 'react-router-dom';
import Overview from '../SideNav/SideOverview';
import { Helmet } from 'react-helmet';
import axios from 'axios';
import { useImmer } from 'use-immer';
import { initUser } from '../../redux/actions/user';
import {
  Grid,
  Feed,
  Icon,
  Form,
  TextArea,
  Button,
  Header,
  Dropdown,
} from 'semantic-ui-react';
import { DateTime } from 'luxon';

const dateSetting = {
  timezone: 'Asia/Singapore',
  locale: 'en-US',
  format: 'FF',
  localeString: DateTime.DATETIME_FULL_WITH_SECONDS,
};

const HomePage = ({ user, initUser, match }) => {
  useEffect(() => {
    const retrieveUser = async () => {
      try {
        const getUser = await axios.get(`/api/auth/getuser`);
        initUser(getUser.data);
      } catch (err) {
        console.error(err.response.data);
      }
    };
    const retrievePosts = async () => {
      try {
        const getPosts = await axios.get('/api/post/getallposts');
        setPosts((draft) => {
          draft.content = getPosts.data;
        });
      } catch (err) {
        console.log(err.response);
      }
    };
    retrieveUser();
    retrievePosts();
  }, []);
  const [post, setPost] = useImmer({ content: '' });
  const [posts, setPosts] = useImmer({ content: [] });
  const onChange = (e) => {
    setPost((draft) => {
      draft.content = e.target.value;
    });
  };

  const onSubmit = async (e) => {
    try {
      e.preventDefault();
      const postStructure = {
        school: user.otherThings.school,
        coverage: 'Singapore',
        name: user.username,
        text: post,
      };
      setPosts((draft) => {
        draft.content.unshift({
          date: DateTime.local()
            .setZone(dateSetting.timezone)
            .setLocale(dateSetting.locale)
            .toFormat(dateSetting.format),
          coverage: 'Singapore',
          school: user.otherThings.school,
          name: user.username,
          text: post.content,
        });
      });
      const result = await axios.post('/api/post/post', postStructure);
      console.log(result.data);
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };

  return (
    <Fragment>
      <Helmet>
        <title>Home Page</title>
      </Helmet>
      {!user.isAuthenticated ? (
        <Redirect to="/" />
      ) : (
        <div>
          <Grid style={{ height: '100vh' }} textAlign="center">
            <Grid.Row columns={2}>
              <Grid.Column>
                <Fragment>
                  <Header as="h1" textAlign="left">
                    Welcome
                  </Header>
                  <Form onSubmit={onSubmit}>
                    <TextArea
                      placeholder="Tell us more"
                      name="post"
                      onChange={onChange}
                    />

                    {/*<Dropdown
                      placeholder="Select Coverage"
                      selection
                      options={[
                        { key: 'school', value: 'NUS', text: 'School' },
                        { key: 'country', value: 'Singapore', text: 'Country' },
                        { key: 'global', value: 'Global', text: 'Global' },
                      ]}
                    />*/}
                    <Button
                      content="POST"
                      color="black"
                      size="large"
                      fluid
                      type="submit"
                    />
                  </Form>
                  <Feed>
                    {posts.content.map((postx, index) => (
                      <Feed.Event key={index}>
                        <Feed.Content>
                          <Feed.Summary>
                            <Link to={`/profile/${postx.userID}`}>
                              {postx.name}
                            </Link>
                            <Feed.Date>{postx.school}</Feed.Date>
                            <Feed.Date>{postx.date}</Feed.Date>
                          </Feed.Summary>
                          <Feed.Extra text>{postx.text}</Feed.Extra>
                        </Feed.Content>
                      </Feed.Event>
                    ))}
                  </Feed>
                </Fragment>
                <Overview userDetails={user} />
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </div>
      )}
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  user: state.user,
});

HomePage.propTypes = {
  user: PropTypes.object.isRequired,
  initUser: PropTypes.func.isRequired,
};

export default connect(mapStateToProps, { initUser })(HomePage);
