import React, { Fragment, useEffect } from 'react';
import {
  Grid,
  Header,
  Button,
  Modal,
  Image,
  Icon,
  Table,
  Item,
  Input,
  Label,
  List,
} from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import { Helmet } from 'react-helmet';
import axios from 'axios';
import { Link } from 'react-router-dom';

const IntelligencePage = ({ match }) => {
  useEffect(() => {
    const getUnits = async () => {
      try {
        const result = await axios.get('/api/users/getmyself');
        setUnits((draft) => {
          draft.recon = result.data.intelligenceDivision.recons;
          draft.commando = result.data.intelligenceDivision.commandos;
        });
      } catch (err) {
        console.error(err.response.data.msg);
      }
    };
    getUnits();
  }, []);
  const [open, setOpen] = React.useState(false);
  const [tasksopen, setTasksOpen] = React.useState(false);
  const [units, setUnits] = useImmer({
    recon: Number,
    commando: Number,
  });
  const [tabs, setTabs] = useImmer({
    profile: true,
    battle: false,
  });
  const [recon, setRecon] = useImmer(0);
  const onChangeRecon = (e) => {
    if (/^\+?(0|[1-9]\d*)$/.test(e.target.value)) {
      setRecon((draft) => e.target.value);
    }
  };
  const [commando, setCommando] = useImmer(0);
  const onChangeCommando = (e) => {
    if (/^\+?(0|[1-9]\d*)$/.test(e.target.value)) {
      setCommando((draft) => e.target.value);
    }
  };

  const onSendRecon = async (e) => {
    try {
      e.preventDefault();
      const result = await axios.post('/api/actions/sendrecons', {
        recon,
        target,
      });
      alert('ok');
      setRecon((draft) => 0);
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };
  const onSendCommando = async (e) => {
    try {
      e.preventDefault();
      const result = await axios.post('/api/actions/sendcommandos', {
        commando,
        target,
      });
      alert('ok');
      setCommando((draft) => 0);
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };

  const [target, setTarget] = useImmer(
    match.params.userid ? match.params.userid : ''
  );
  const onChangeTarget = (e) => {
    setTarget((draft) => e.target.value);
  };
  const [intelTasks, setIntelTasks] = useImmer([]);
  const onGetIntelTasks = async () => {
    try {
      const result = await axios.get('/api/actions/getinteltasks');
      setIntelTasks((draft) => result.data);
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };

  const [intel, setIntel] = useImmer([]);
  const getIntels = async () => {
    try {
      const result = await axios.get('/api/actions/getintelreports');
      console.log(result.data[0].intelligenceDivision.intels);
      setIntel((draft) => result.data[0].intelligenceDivision.intels);
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };
  const [saboTasks, setSaboTasks] = useImmer([]);
  const onGetSaboTasks = async () => {
    try {
      const result = await axios.get('/api/actions/getsabotagetasks');
      setSaboTasks((draft) => result.data);
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };

  const [missions, setMissions] = useImmer([]);
  const onGetMissions = async () => {
    try {
      const result = await axios.get('api/actions/getsabotagemissions');
      setMissions((draft) => result.data[0].intelligenceDivision.sabotages);
    } catch (err) {
      console.error(err.response.data.msg);
    }
  };

  const profileTab = (
    <>
      <Grid.Column>
        <Input label="Target:" value={target} onChange={onChangeTarget} />
        <Table celled padded>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Description</Table.HeaderCell>
              <Table.HeaderCell>You have</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            <Table.Row>
              <Table.Cell>
                <Header>Recon</Header>
              </Table.Cell>
              <Table.Cell>
                <Header>{units.recon}</Header>
              </Table.Cell>
              <Table.Cell>
                <Input
                  placeholder="Amount..."
                  type="number"
                  value={recon}
                  onChange={onChangeRecon}
                />
              </Table.Cell>
            </Table.Row>
          </Table.Body>
        </Table>

        <Button color="black" fluid onClick={onSendRecon}>
          Send
        </Button>
        <br />
        <Modal
          open={tasksopen}
          onClose={() => setTasksOpen(false)}
          onOpen={() => {
            setTasksOpen(true);
            onGetIntelTasks();
          }}
          trigger={
            <Button color="red" fluid>
              On-going tasks
            </Button>
          }
        >
          <Modal.Header>On-going tasks</Modal.Header>
          <Modal.Content scrolling>
            <Modal.Description>
              <Table celled padded>
                <Table.Header>
                  <Table.Row>
                    <Table.HeaderCell>Amount</Table.HeaderCell>
                    <Table.HeaderCell>Target</Table.HeaderCell>
                    <Table.HeaderCell>Return In</Table.HeaderCell>
                    <Table.HeaderCell>Date</Table.HeaderCell>
                  </Table.Row>
                </Table.Header>
                <Table.Body>
                  {intelTasks.map((task, index) => (
                    <Table.Row key={index}>
                      <Table.Cell>{task.forces[0].amount} units</Table.Cell>
                      <Table.Cell>
                        <Link to={`/profile/${task.target._id}`}>
                          {task.target.name}
                        </Link>
                      </Table.Cell>
                      <Table.Cell>{task.doneInWhatTick}</Table.Cell>
                      <Table.Cell>{task.date}</Table.Cell>
                    </Table.Row>
                  ))}
                </Table.Body>
              </Table>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={() => setTasksOpen(false)} color="black">
              Close <Icon name="close" />
            </Button>
          </Modal.Actions>
        </Modal>

        <Modal
          open={open}
          onClose={() => setOpen(false)}
          onOpen={() => {
            setOpen(true);
            getIntels();
          }}
          trigger={
            <Button color="black" fluid>
              Intels gathered
            </Button>
          }
        >
          <Modal.Header>Intels gathered</Modal.Header>
          <Modal.Content scrolling>
            <Modal.Description>
              <Table celled padded>
                <Table.Header>
                  <Table.Row>
                    <Table.HeaderCell>Result</Table.HeaderCell>
                    <Table.HeaderCell>Description</Table.HeaderCell>
                    <Table.HeaderCell>Target</Table.HeaderCell>
                    <Table.HeaderCell>Date</Table.HeaderCell>
                  </Table.Row>
                </Table.Header>
                <Table.Body>
                  {intel.map((info, index) => (
                    <Table.Row key={index}>
                      <Table.Cell>
                        <Header as="h2">{info.result}</Header>
                      </Table.Cell>

                      <Table.Cell>
                        <List>
                          <List.Item>
                            <b>Intelligence Division</b>
                          </List.Item>
                          <List.Item>
                            <List.List>
                              <List.Item>
                                Recons: {info.intelligenceDivision.recons}
                              </List.Item>
                              <List.Item>
                                Commandos: {info.intelligenceDivision.commandos}
                              </List.Item>
                            </List.List>
                          </List.Item>
                        </List>
                        <List>
                          <List.Item>
                            <b>Infantry Division</b>
                          </List.Item>
                          <List.Item>
                            <List.List>
                              <List.Item>
                                Attack Points: {info.infantryDivision.attackPts}
                              </List.Item>
                              <List.Item>
                                Defence Points:{' '}
                                {info.infantryDivision.defencePts}
                              </List.Item>
                            </List.List>
                          </List.Item>
                        </List>
                        <List>
                          <List.Item>
                            <b>Air Division</b>
                          </List.Item>
                          <List.Item>
                            <List.List>
                              <List.Item>
                                Attack Points: {info.airDivision.attackPts}
                              </List.Item>
                              <List.Item>
                                Defence Points:{info.airDivision.defencePts}
                              </List.Item>
                            </List.List>
                          </List.Item>
                        </List>
                        <List>
                          <List.Item>
                            <b>Sea Division</b>
                          </List.Item>
                          <List.Item>
                            <List.List>
                              <List.Item>
                                Attack Points: {info.navalDivision.attackPts}
                              </List.Item>
                              <List.Item>
                                Defence Points: {info.navalDivision.defencePts}
                              </List.Item>
                            </List.List>
                          </List.Item>
                        </List>
                      </Table.Cell>
                      <Table.Cell singleLine>
                        <Link to={`/profile/${info.target._id}`}>
                          {info.target.name}
                        </Link>
                      </Table.Cell>
                      <Table.Cell>{info.date}</Table.Cell>
                    </Table.Row>
                  ))}
                </Table.Body>
              </Table>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={() => setOpen(false)} color="black">
              Close <Icon name="close" />
            </Button>
          </Modal.Actions>
        </Modal>
      </Grid.Column>
    </>
  );
  const battleTab = (
    <>
      <Grid.Column>
        <Input label="Target:" value={target} />
        <Table celled padded>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Description</Table.HeaderCell>
              <Table.HeaderCell>You have</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            <Table.Row>
              <Table.Cell>
                <Header>Commandos</Header>
              </Table.Cell>
              <Table.Cell>
                <Header>{units.commando}</Header>
              </Table.Cell>
              <Table.Cell>
                <Input
                  placeholder="Amount..."
                  type="number"
                  value={commando}
                  onChange={onChangeCommando}
                />
              </Table.Cell>
            </Table.Row>
          </Table.Body>
        </Table>
        <Button color="black" fluid onClick={onSendCommando}>
          Send
        </Button>
        <br />
        <Modal
          open={tasksopen}
          onClose={() => setTasksOpen(false)}
          onOpen={() => {
            setTasksOpen(true);
            onGetSaboTasks();
          }}
          trigger={
            <Button color="red" fluid>
              On-going tasks
            </Button>
          }
        >
          <Modal.Header>On-going tasks</Modal.Header>
          <Modal.Content scrolling>
            <Modal.Description>
              <Table celled padded>
                <Table.Header>
                  <Table.Row>
                    <Table.HeaderCell>Amount</Table.HeaderCell>
                    <Table.HeaderCell>Target</Table.HeaderCell>
                    <Table.HeaderCell>Return In</Table.HeaderCell>
                    <Table.HeaderCell>Date</Table.HeaderCell>
                  </Table.Row>
                </Table.Header>
                <Table.Body>
                  {saboTasks.map((task, index) => (
                    <Table.Row key={index}>
                      <Table.Cell>{task.forces[0].amount} units</Table.Cell>
                      <Table.Cell>
                        <Link to={`/profile/${task.target._id}`}>
                          {task.target.name}
                        </Link>
                      </Table.Cell>
                      <Table.Cell>{task.doneInWhatTick}</Table.Cell>
                      <Table.Cell>{task.date}</Table.Cell>
                    </Table.Row>
                  ))}
                </Table.Body>
              </Table>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={() => setTasksOpen(false)} color="black">
              Close <Icon name="close" />
            </Button>
          </Modal.Actions>
        </Modal>

        <Modal
          open={open}
          onClose={() => setOpen(false)}
          onOpen={() => {
            setOpen(true);
            onGetMissions();
          }}
          trigger={
            <Button color="black" fluid>
              Missions
            </Button>
          }
        >
          <Modal.Header>Missions</Modal.Header>
          <Modal.Content scrolling>
            <Modal.Description>
              <Table celled padded>
                <Table.Header>
                  <Table.Row>
                    <Table.HeaderCell>Result</Table.HeaderCell>
                    <Table.HeaderCell>Description</Table.HeaderCell>
                    <Table.HeaderCell>Target</Table.HeaderCell>
                    <Table.HeaderCell>Date</Table.HeaderCell>
                  </Table.Row>
                </Table.Header>
                <Table.Body>
                  {missions.map((mission, index) => (
                    <Table.Row>
                      <Table.Cell>
                        <Header as="h2" textAlign="center">
                          {mission.result}
                        </Header>
                      </Table.Cell>
                      <Table.Cell>{mission.description}</Table.Cell>
                      <Table.Cell>
                        <Link to={`/profile/${mission.target._id}`}>
                          {mission.target.name}
                        </Link>
                      </Table.Cell>
                      <Table.Cell>{mission.date}</Table.Cell>
                    </Table.Row>
                  ))}
                </Table.Body>
              </Table>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={() => setOpen(false)} color="black">
              Close <Icon name="close" />
            </Button>
          </Modal.Actions>
        </Modal>
      </Grid.Column>
    </>
  );

  const onTab = (e) => {
    e.preventDefault();
    setTabs((draft) => {
      draft[e.target.name] = true;
      if (e.target.name === 'profile') {
        draft.battle = false;
      } else {
        draft.profile = false;
      }
    });
  };
  return (
    <Fragment>
      <Helmet>
        <title>Profile</title>
      </Helmet>
      <Grid
        textAlign="center"
        verticalAlign="middle"
        style={{ height: '100vh' }}
        columns={2}
        divided
      >
        <Grid.Row style={{ maxWidth: 600 }}>
          <Grid.Column>
            <Button name="profile" onClick={onTab}>
              Recon
            </Button>
            <Button name="battle" onClick={onTab}>
              Sabotage
            </Button>
          </Grid.Column>
          {tabs.profile && profileTab}
          {tabs.battle && battleTab}
        </Grid.Row>
      </Grid>
    </Fragment>
  );
};

export default IntelligencePage;
