import React, { useEffect } from 'react';
import { Grid, Header, Dropdown, Table } from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import { Link } from 'react-router-dom';
import axios from 'axios';

const ScoreboardPage = () => {
  const [users, setUsers] = useImmer([]);
  useEffect(() => {
    const getUsers = async () => {
      const retrievedUsers = await axios.get('/api/users/getusers');
      setUsers((draft) => retrievedUsers.data);
    };
    getUsers();
  }, []);
  return (
    <Grid textAlign="center" verticalAlign="middle" style={{ height: '100vh' }}>
      <Grid.Column style={{ maxWidth: 550 }}>
        <Header as="h3">ScoreboardPage</Header>
        {/* <Dropdown
          placeholder="Select Coverage"
          selection
          options={[
            { key: 'school', value: 'NUS', text: 'School' },
            { key: 'country', value: 'Singapore', text: 'Country' },
            { key: 'global', value: 'Global', text: 'Global' },
          ]}
        />*/}
        <Table celled padded>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Rank</Table.HeaderCell>
              <Table.HeaderCell>Name</Table.HeaderCell>
              <Table.HeaderCell>School</Table.HeaderCell>
              <Table.HeaderCell>Country</Table.HeaderCell>
              <Table.HeaderCell>Networth</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {users.map((user, index) => (
              <Table.Row key={index}>
                <Table.Cell>{index + 1}</Table.Cell>
                <Table.Cell>
                  <Header sub>
                    <Link to={`/profile/${user._id}`}>{user.name}</Link>
                  </Header>
                </Table.Cell>
                <Table.Cell>
                  <Header sub>{user.school}</Header>
                </Table.Cell>
                <Table.Cell>
                  <Header sub>{user.country}</Header>
                </Table.Cell>
                <Table.Cell>
                  <Header sub>{user.networth}</Header>
                </Table.Cell>
              </Table.Row>
            ))}
          </Table.Body>
        </Table>
      </Grid.Column>
    </Grid>
  );
};

export default ScoreboardPage;
