import React, { useEffect, Fragment } from 'react';
import axios from 'axios';
import { useImmer } from 'use-immer';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Grid, Header } from 'semantic-ui-react';

import LoginPage from './components/authPage/AuthPage';
import SignUp from './components/SignupPage/SignupPage';
import HomePage from './components/HomePage/HomePage';
import ControlPanel from './components/controlPanel/ControlPanel';
import ExploreLandPage from './components/GameUI/ExploreLand/ExploreLand';
import MyMessagesPage from './components/GameUI/MyMessages/MyMessages';
import BuildingsPage from './components/GameUI/Buildings/Buildings';
import MilitaryPage from './components/GameUI/Military/MilitaryPage';
import AttackPage from './components/GameUI/Attack/Attack';
import ScoreboardPage from './components/GameUI/Scoreboard/Scoreboard';
import ProfilePage from './components/GameUI/Profile/Profile';
import CreateEditProfilePage from './components/GameUI/Profile/CreateEditProfile';
import IntelligencePage from './components/GameUI/Intelligence/Intelligence';
import NewsPage from './components/GameUI/News/News';
import AskQuestions from './components/askQuestions/AskQuestions';
import JobListing from './components/jobListing/JobListing';
import AskPage from './components/askQuestions/SpecificPage';
import Feedback from './components/Feedback/Feedback';

//Redux
import { Provider } from 'react-redux';
import store from './redux/store';
import SideNav from './components/SideNav/SideNav';

import { initUser, resetUser } from './redux/actions/user';

const App = () => {
  const [loadingStatus, setLoadingStatus] = useImmer({
    done: false,
    isAuth: false,
  });

  useEffect(() => {
    async function testingFunc() {
      try {
        const user = await axios.get('/api/auth/getuser');
        console.log('user', user);
        store.dispatch(initUser(user.data));
      } catch (err) {
        store.dispatch(resetUser());
      }
      setLoadingStatus((draft) => {
        draft.done = true;
        draft.isAuth = store.getState().user.isAuthenticated;
      });
    }
    testingFunc();
  }, [setLoadingStatus]);
  return (
    <Provider store={store}>
      {loadingStatus.done ? (
        <Fragment>
          <Router>
            <Switch>
              <Route exact path="/" component={LoginPage} />
              <Route exact path="/signup" component={SignUp} />
              <SideNav>
                <Route exact path="/homepage" component={HomePage} />
                <Route exact path="/control" component={ControlPanel} />
                <Route exact path="/explore" component={ExploreLandPage} />
                <Route exact path="/messages" component={MyMessagesPage} />
                <Route exact path="/buildings" component={BuildingsPage} />
                <Route exact path="/military" component={MilitaryPage} />
                <Route exact path="/attack" component={AttackPage} />
                <Route exact path="/attack/:userid" component={AttackPage} />
                <Route exact path="/scoreboard" component={ScoreboardPage} />
                <Route exact path="/profile/:userid" component={ProfilePage} />
                <Route
                  exact
                  path="/ceprofile"
                  component={CreateEditProfilePage}
                />
                <Route exact path="/news" component={NewsPage} />
                <Route
                  exact
                  path="/intelligence"
                  component={IntelligencePage}
                />
                <Route
                  exact
                  path="/intelligence/:userid"
                  component={IntelligencePage}
                />
                <Route exact path="/askquestions" component={AskQuestions} />
                <Route exact path="/askpage/:name" component={AskPage} />
                <Route exact path="/joblistings" component={JobListing} />
                <Route exact path="/feedback" component={Feedback} />
              </SideNav>
            </Switch>
          </Router>
        </Fragment>
      ) : (
        <Grid
          textAlign="center"
          verticalAlign="middle"
          style={{ height: '100vh' }}
        >
          <Grid.Column style={{ maxWidth: 450 }}>
            <Header as="h3">Loading....</Header>
          </Grid.Column>
        </Grid>
      )}
    </Provider>
  );
};

export default App;
