import axios from 'axios';
import { LOGIN, INIT_USER, RESET_USER, SIGNUP } from './types';

//Login user
export const loginUser = (formData, history) => async (dispatch) => {
  try {
    const res = await axios.post('/api/auth/login', formData);

    dispatch({
      type: LOGIN,
      payload: res.data,
    });
    //history.push('/admin/controlpanel');
    history.push(`/homepage`);
  } catch (err) {
    alert('Invalid Account');
  }
};

//Initialize User info when refreshed
export const initUser = (data) => (dispatch) => {
  dispatch({
    type: INIT_USER,
    payload: data,
  });
};

//Reset / Logout user
export const resetUser = () => (dispatch) => {
  dispatch({
    type: RESET_USER,
  });
};

//Sign up user
export const signup = (formData, history) => async (dispatch) => {
  try {
    const res = await axios.post('/api/auth/register', formData);
    dispatch({
      type: SIGNUP,
      payload: res.data,
    });
    history.push(`/homepage`);
  } catch (err) {
    alert(err.response.data.msg);
  }
};
