import React from 'react';
import {
  Grid,
  Header,
  Input,
  Modal,
  Button,
  Icon,
  Table,
  Item,
} from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import axios from 'axios';

const ExploreLand = ({ history }) => {
  const [open, setOpen] = React.useState(false);
  const [population, setPopulation] = useImmer(
    history.location.state.otherThings.population
  );
  const [sendingAmt, setSendingAmt] = useImmer(0);
  const [totalCost, setTotalCost] = useImmer(0);
  const [tasks, setTasks] = useImmer([]);

  const EXPLORER_COST = 500;

  const getExploringTasks = async () => {
    try {
      const retrievedTasks = await axios.get('/api/actions/getexploringtasks');
      setTasks((draft) => retrievedTasks.data);
    } catch (err) {
      alert(JSON.stringify(err.response.data));
    }
  };
  const onChange = (e) => {
    if (/^\+?(0|[1-9]\d*)$/.test(e.target.value)) {
      setSendingAmt((draft) => e.target.value);
      setTotalCost((draft) => e.target.value * EXPLORER_COST);
    }
  };

  const onSend = async (e) => {
    try {
      e.preventDefault();
      const structure = {
        creation: {
          amount: sendingAmt,
        },
        totalCost: totalCost,
      };
      console.log('Data', sendingAmt);
      const sendExplorer = await axios.post(
        '/api/actions/sendexplorer',
        structure
      );
      console.log(sendExplorer.data);
      setSendingAmt((draft) => 0);
      setPopulation((draft) => (draft -= sendingAmt));
      alert('ok');
    } catch (err) {
      alert(JSON.stringify(err.response.data.msg));
    }
  };

  return (
    <Grid textAlign="center" verticalAlign="middle" style={{ height: '100vh' }}>
      <Grid.Row>
        <Grid.Column style={{ maxWidth: 450 }}>
          <Header as="h1">Explore Land</Header>
          <>
            <Header>Available Units: {population}</Header>
            <Header>1 unit = $500</Header>
          </>
          <>
            <Input
              label="Amount:"
              value={sendingAmt}
              onChange={onChange}
              type="number"
            />
            <Button color="black" onClick={onSend}>
              Send
            </Button>
          </>
          <br />
          <br />
          <Modal
            open={open}
            onClose={() => setOpen(false)}
            onOpen={() => {
              setOpen(true);
              getExploringTasks();
            }}
            trigger={<Button color="black">View On-going tasks</Button>}
          >
            <Modal.Header>Recent Battles</Modal.Header>
            <Modal.Content scrolling>
              <Modal.Description>
                <Table celled padded>
                  <Table.Header>
                    <Table.Row>
                      <Table.HeaderCell>Amount</Table.HeaderCell>
                      <Table.HeaderCell>Returning In</Table.HeaderCell>
                      <Table.HeaderCell>Date</Table.HeaderCell>
                    </Table.Row>
                  </Table.Header>
                  <Table.Body>
                    {tasks.map((task) => (
                      <Table.Row>
                        <Table.Cell>
                          <Header>
                            {task.creation.amount} units (
                            {task.creation.amount * 5} land)
                          </Header>
                        </Table.Cell>
                        <Table.Cell singleLine>
                          {task.doneInWhatTick} Ticks
                        </Table.Cell>
                        <Table.Cell>{task.date}</Table.Cell>
                      </Table.Row>
                    ))}
                  </Table.Body>
                </Table>
              </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
              <Button onClick={() => setOpen(false)} color="black">
                Close <Icon name="close" />
              </Button>
            </Modal.Actions>
          </Modal>
        </Grid.Column>
      </Grid.Row>
    </Grid>
  );
};

export default ExploreLand;
