import { LOGIN, INIT_USER, RESET_USER, SIGNUP } from '../actions/types';
import produce from 'immer';

const initialState = {
  userid: '',
  username: '',
  email: '',
  experiences: [],
  otherThings: {},
  isAuthenticated: false,
};

const user = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case SIGNUP:
      case INIT_USER:
      case LOGIN:
        draft.userid = action.payload._id;
        draft.username = action.payload.name;
        draft.email = action.payload.email;
        draft.experiences = action.payload.experiences;
        draft.otherThings = action.payload;
        draft.isAuthenticated = true;
        break;

      case RESET_USER:
        draft.userid = '';
        draft.username = '';
        draft.email = '';
        draft.experiences = [];
        draft.otherThings = {};
        draft.isAuthenticated = false;
        break;

      default:
        break;
    }
  });

export default user;
