import axios from 'axios';
import { GETITEMS } from './types';

export const getAllItemsByEmail = () => async (dispatch) => {
  try {
    const res = await axios.get('/api/items/getall');
    dispatch({
      type: GETITEMS,
      payload: res.data,
    });
  } catch (err) {
    console.log('Error: ', err.response.data);
  }
};
