import produce from 'immer';
import { ADDTASKS } from '../actions/types';

const initialState = {
  tasks: [],
  isLoading: true,
};

const tasks = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case ADDTASKS:
        draft.tasks = [{ testing: 'tasking' }];
        draft.isLoading = false;
        break;
      default:
        break;
    }
  });

export default tasks;
