import React, { useEffect } from 'react';
import {
  Grid,
  Header,
  Table,
  Input,
  Button,
  Modal,
  Icon,
} from 'semantic-ui-react';
import { useImmer } from 'use-immer';
import axios from 'axios';

const MilitaryPage = () => {
  useEffect(() => {
    const getUnits = async () => {
      const result = await axios.get('/api/users/getmyself');
      setSpaces((draft) => result.data.buildings);
      setUnits((draft) => {
        draft.recon = result.data.intelligenceDivision.recons;
        draft.commando = result.data.intelligenceDivision.commandos;
        draft.infanOne = result.data.infantryDivision.infantry1.quantity;
        draft.infanTwo = result.data.infantryDivision.infantry2.quantity;
        draft.infanThree = result.data.infantryDivision.infantry3.quantity;
        draft.infanFour = result.data.infantryDivision.infantry4.quantity;
        draft.airOne = result.data.airDivision.air1.quantity;
        draft.airTwo = result.data.airDivision.air2.quantity;
        draft.airThree = result.data.airDivision.air3.quantity;
        draft.airFour = result.data.airDivision.air4.quantity;
        draft.seaOne = result.data.seaDivision.sea1.quantity;
        draft.seaTwo = result.data.seaDivision.sea2.quantity;
        draft.seaThree = result.data.seaDivision.sea3.quantity;
        draft.seaFour = result.data.seaDivision.sea4.quantity;
      });
      setLoading((draft) => false);
    };
    getUnits();
  }, []);
  const [loading, setLoading] = useImmer(true);
  const [spaces, setSpaces] = useImmer({});
  const [units, setUnits] = useImmer({
    recon: Number,
    commando: Number,
    infanOne: Number,
    infanTwo: Number,
    infanThree: Number,
    infanFour: Number,
    airOne: Number,
    airTwo: Number,
    airThree: Number,
    airFour: Number,
    seaOne: Number,
    seaTwo: Number,
    seaThree: Number,
    seaFour: Number,
  });
  const [open, setOpen] = React.useState(false);

  const getMilitaryTasks = async () => {
    try {
      const result = await axios.get('/api/actions/getmilitarytasks');
      console.log(result.data);
      setTasks((draft) => result.data);
    } catch (err) {
      console.error(err.response);
    }
  };
  const [tasks, setTasks] = useImmer([]);

  const [intelligenceDiv, setIntelDiv] = useImmer({
    recon: 0,
    commando: 0,
  });

  const [infantryDiv, setInfantryDiv] = useImmer({
    infantryOne: 0,
    infantryTwo: 0,
    infantryThree: 0,
    infantryFour: 0,
  });
  const [airDiv, setAirDiv] = useImmer({
    airOne: 0,
    airTwo: 0,
    airThree: 0,
    airFour: 0,
  });
  const [navalDiv, setNavalDiv] = useImmer({
    seaOne: 0,
    seaTwo: 0,
    seaThree: 0,
    seaFour: 0,
  });

  const onChangeIntelDiv = (e) => {
    setIntelDiv((draft) => {
      if (/^\+?(0|[1-9]\d*)$/.test(e.target.value)) {
        draft[e.target.name] = e.target.value;
      }
    });
  };
  const onChangeInfanDiv = (e) => {
    setInfantryDiv((draft) => {
      if (/^\+?(0|[1-9]\d*)$/.test(e.target.value)) {
        draft[e.target.name] = e.target.value;
      }
    });
  };
  const onChangeAirDiv = (e) => {
    setAirDiv((draft) => {
      if (/^\+?(0|[1-9]\d*)$/.test(e.target.value)) {
        draft[e.target.name] = e.target.value;
      }
    });
  };
  const onChangeNavalDiv = (e) => {
    setNavalDiv((draft) => {
      if (/^\+?(0|[1-9]\d*)$/.test(e.target.value)) {
        draft[e.target.name] = e.target.value;
      }
    });
  };

  const onSubmit = async (e) => {
    try {
      e.preventDefault();
      const res = await axios.post('/api/actions/createmilitary', {
        intelligenceDiv,
        infantryDiv,
        airDiv,
        navalDiv,
      });
      setInfantryDiv((draft) => {
        draft.infantryOne = 0;
        draft.infantryTwo = 0;
        draft.infantryThree = 0;
        draft.infantryFour = 0;
      });
      setAirDiv((draft) => {
        draft.airOne = 0;
        draft.airTwo = 0;
        draft.airThree = 0;
        draft.airFour = 0;
      });
      setNavalDiv((draft) => {
        draft.seaOne = 0;
        draft.seaTwo = 0;
        draft.seaThree = 0;
        draft.seaFour = 0;
      });
      alert('ok');
    } catch (err) {
      alert(err.response.data.msg);
    }
  };

  return (
    <Grid
      textAlign="center"
      verticalAlign="middle"
      style={{ height: '100vh' }}
      columns={3}
    >
      {loading ? (
        <Header>Loading...</Header>
      ) : (
        <Grid.Column style={{ maxWidth: 550 }}>
          <Header as="h3">MilitaryPage</Header>
          <Header>Intelligence Division</Header>
          <Table celled padded>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Description</Table.HeaderCell>
                <Table.HeaderCell>Cost</Table.HeaderCell>
                <Table.HeaderCell>Space Cost</Table.HeaderCell>
                <Table.HeaderCell>Available Space</Table.HeaderCell>
                <Table.HeaderCell>You have</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              <Table.Row>
                <Table.Cell>
                  <Header>Reconn</Header>
                </Table.Cell>
                <Table.Cell singleLine>$100</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>
                  {spaces.intelligenceCamp.availableSpace}
                </Table.Cell>
                <Table.Cell>{units.recon}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="recon"
                    value={intelligenceDiv.recon}
                    onChange={onChangeIntelDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Commando</Header>
                </Table.Cell>
                <Table.Cell singleLine>$100</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>
                  {spaces.intelligenceCamp.availableSpace}
                </Table.Cell>
                <Table.Cell>{units.commando}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="commando"
                    value={intelligenceDiv.commando}
                    onChange={onChangeIntelDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
            </Table.Body>
          </Table>
          <Header>Infantry Division</Header>
          <Table celled padded>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Description</Table.HeaderCell>

                <Table.HeaderCell>Cost</Table.HeaderCell>
                <Table.HeaderCell>Space Cost</Table.HeaderCell>
                <Table.HeaderCell>Available Space</Table.HeaderCell>
                <Table.HeaderCell>Attack Pts</Table.HeaderCell>
                <Table.HeaderCell>Defence Pts</Table.HeaderCell>
                <Table.HeaderCell>You have</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              <Table.Row>
                <Table.Cell>
                  <Header>Infantry 1</Header>
                </Table.Cell>
                <Table.Cell singleLine>$100</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.infantryCamp.availableSpace}</Table.Cell>
                <Table.Cell>10</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.infanOne}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="infantryOne"
                    value={infantryDiv.infantryOne}
                    onChange={onChangeInfanDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Infantry 2</Header>
                </Table.Cell>
                <Table.Cell singleLine>$200</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.infantryCamp.availableSpace}</Table.Cell>
                <Table.Cell>20</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.infanTwo}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="infantryTwo"
                    value={infantryDiv.infantryTwo}
                    onChange={onChangeInfanDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Infantry 3</Header>
                </Table.Cell>
                <Table.Cell singleLine>$300</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.infantryCamp.availableSpace}</Table.Cell>
                <Table.Cell>30</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.infanThree}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="infantryThree"
                    value={infantryDiv.infantryThree}
                    onChange={onChangeInfanDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Infantry 4</Header>
                </Table.Cell>
                <Table.Cell singleLine>$400</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.infantryCamp.availableSpace}</Table.Cell>
                <Table.Cell>40</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.infanFour}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="infantryFour"
                    value={infantryDiv.infantryFour}
                    onChange={onChangeInfanDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
            </Table.Body>
          </Table>
          <Header>Air Division</Header>
          <Table celled padded>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Description</Table.HeaderCell>

                <Table.HeaderCell>Cost</Table.HeaderCell>
                <Table.HeaderCell>Space Cost</Table.HeaderCell>
                <Table.HeaderCell>Available Space</Table.HeaderCell>
                <Table.HeaderCell>Attack Pts</Table.HeaderCell>
                <Table.HeaderCell>Defence Pts</Table.HeaderCell>

                <Table.HeaderCell>You have</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              <Table.Row>
                <Table.Cell>
                  <Header>Air 1</Header>
                </Table.Cell>
                <Table.Cell singleLine>$100</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.airField.availableSpace}</Table.Cell>
                <Table.Cell>10</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.airOne}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="airOne"
                    value={airDiv.airOne}
                    onChange={onChangeAirDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Air 2</Header>
                </Table.Cell>
                <Table.Cell singleLine>$200</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.airField.availableSpace}</Table.Cell>
                <Table.Cell>20</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.airTwo}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="airTwo"
                    value={airDiv.airTwo}
                    onChange={onChangeAirDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Air 3</Header>
                </Table.Cell>
                <Table.Cell singleLine>$300</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.airField.availableSpace}</Table.Cell>
                <Table.Cell>30</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.airThree}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="airThree"
                    value={airDiv.airThree}
                    onChange={onChangeAirDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Air 4</Header>
                </Table.Cell>
                <Table.Cell singleLine>$400</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.airField.availableSpace}</Table.Cell>
                <Table.Cell>40</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.airFour}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="airFour"
                    value={airDiv.airFour}
                    onChange={onChangeAirDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
            </Table.Body>
          </Table>
          <Header>Naval Division</Header>
          <Table celled padded>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Description</Table.HeaderCell>

                <Table.HeaderCell>Cost</Table.HeaderCell>
                <Table.HeaderCell>Space Cost</Table.HeaderCell>
                <Table.HeaderCell>Available Space</Table.HeaderCell>
                <Table.HeaderCell>Attack Pts</Table.HeaderCell>
                <Table.HeaderCell>Defence Pts</Table.HeaderCell>
                <Table.HeaderCell>You have</Table.HeaderCell>
              </Table.Row>
            </Table.Header>
            <Table.Body>
              <Table.Row>
                <Table.Cell>
                  <Header>Sea 1</Header>
                </Table.Cell>
                <Table.Cell singleLine>$100</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.navalBase.availableSpace}</Table.Cell>
                <Table.Cell>10</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.seaOne}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="seaOne"
                    value={navalDiv.seaOne}
                    onChange={onChangeNavalDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Sea 2</Header>
                </Table.Cell>
                <Table.Cell singleLine>$200</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.navalBase.availableSpace}</Table.Cell>
                <Table.Cell>20</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.seaTwo}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="seaTwo"
                    value={navalDiv.seaTwo}
                    onChange={onChangeNavalDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Sea 3</Header>
                </Table.Cell>
                <Table.Cell singleLine>$300</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.navalBase.availableSpace}</Table.Cell>
                <Table.Cell>30</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.seaThree}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="seaThree"
                    value={navalDiv.seaThree}
                    onChange={onChangeNavalDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
              <Table.Row>
                <Table.Cell>
                  <Header>Sea 4</Header>
                </Table.Cell>
                <Table.Cell singleLine>$400</Table.Cell>
                <Table.Cell>1</Table.Cell>
                <Table.Cell>{spaces.navalBase.availableSpace}</Table.Cell>
                <Table.Cell>40</Table.Cell>
                <Table.Cell>5</Table.Cell>

                <Table.Cell>{units.seaFour}</Table.Cell>
                <Table.Cell>
                  <Input
                    placeholder="Amount..."
                    name="seaFour"
                    value={navalDiv.seaFour}
                    onChange={onChangeNavalDiv}
                    type="number"
                  />
                </Table.Cell>
              </Table.Row>
            </Table.Body>
          </Table>
          <Button color="black" fluid onClick={onSubmit}>
            Build
          </Button>
          <Modal
            open={open}
            onClose={() => setOpen(false)}
            onOpen={() => {
              setOpen(true);
              getMilitaryTasks();
            }}
            trigger={
              <Button color="red" fluid>
                View On-going tasks
              </Button>
            }
          >
            <Modal.Header>Recent Battles</Modal.Header>
            <Modal.Content scrolling>
              <Modal.Description>
                <Table celled padded>
                  <Table.Header>
                    <Table.Row>
                      <Table.HeaderCell>Building</Table.HeaderCell>
                      <Table.HeaderCell>Amount</Table.HeaderCell>
                      <Table.HeaderCell>Returning In</Table.HeaderCell>
                      <Table.HeaderCell>Date</Table.HeaderCell>
                    </Table.Row>
                  </Table.Header>
                  <Table.Body>
                    {tasks.map((task, index) => (
                      <Table.Row key={index}>
                        <Table.Cell>
                          <Header>{task.creation.name}</Header>
                        </Table.Cell>
                        <Table.Cell singleLine>
                          {task.creation.amount}{' '}
                        </Table.Cell>
                        <Table.Cell singleLine>
                          {task.doneInWhatTick} Tick
                        </Table.Cell>
                        <Table.Cell>{task.date}</Table.Cell>
                      </Table.Row>
                    ))}
                  </Table.Body>
                </Table>
              </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
              <Button onClick={() => setOpen(false)} color="black">
                Close <Icon name="close" />
              </Button>
            </Modal.Actions>
          </Modal>
        </Grid.Column>
      )}
    </Grid>
  );
};

export default MilitaryPage;
