import React, { Fragment, useEffect } from 'react';
import {
  Grid,
  Header,
  Form,
  TextArea,
  Button,
  Comment,
  Card,
} from 'semantic-ui-react';

import { useImmer } from 'use-immer';
import axios from 'axios';

const SpecificPage = ({ match }) => {
  useEffect(() => {
    const getQnA = async () => {
      try {
        const result = await axios.get(
          `/api/ask/getQuestions/${match.params.name}`
        );
        setQuestions((draft) => result.data);
      } catch (err) {
        console.error(err.response.data.msg);
      }
    };
    getQnA();
  }, []);
  const [questions, setQuestions] = useImmer([]);
  const [question, setQuestion] = useImmer('');

  const onChange = (e) => {
    setQuestion((draft) => e.target.value);
  };
  const onSubmit = async (e) => {
    try {
      const structure = {
        question: question,
        to: 'ben',
      };
      const res = await axios.post('/api/ask/sendQuestion', structure);
      alert('Question submitted. Will be here when its replied.');
      setQuestion((draft) => '');
    } catch (err) {
      console.log(err.response.data.msg);
    }
  };
  return (
    <Grid textAlign="center" style={{ height: '100vh' }}>
      <Grid.Column style={{ maxWidth: 650, float: 'left' }}>
        <Header as="h1">Ask {match.params.name} </Header>

        <Form onSubmit={onSubmit}>
          <TextArea
            placeholder="What do you wanna ask?"
            value={question}
            onChange={onChange}
          />
          <Button color="facebook" type="submit">
            Submit
          </Button>
        </Form>
        <Header sub>Will post them here when it is replied.</Header>
        <Comment.Group>
          <Header as="h3" dividing>
            QnA
          </Header>
          <Card.Group>
            {questions.map((qns, index) => (
              <Card fluid key={index}>
                <Card.Content textAlign="left">
                  <Card.Header>
                    {index + 1}. {qns.question}
                  </Card.Header>
                  <Card.Description>{qns.reply}</Card.Description>
                </Card.Content>
              </Card>
            ))}
          </Card.Group>
        </Comment.Group>
      </Grid.Column>
    </Grid>
  );
};

export default SpecificPage;
